import { Component } from '@angular/core';

@Component({
  selector: 'miqvmt-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class MiQVMTHeaderComponent {
  private sentences:string[] = ["Que no le da...",
                                "Menudo siroco...",
                                "El puto amo..."];
  private sentence:string = this.sentences[0];
  private index = 0;
  private grow = false;

  constructor() {
    setInterval(() => {
      let sentence = this.sentence,
          length = sentence.length;
          if(length == 0 ) {
            this.grow = true;
            ++this.index;
            this.index = this.index % this.sentences.length;
            this.sentence = this.sentences[this.index].substring(0, 1);
          } else {
            if(length == this.sentences[this.index].length)
              this.grow = false;
            
            if(this.grow)
              this.sentence = this.sentences[this.index].substring(0, length +1);
            else
              this.sentence = sentence.substring(0, length -1);
          }
    }, 100);
  }

}



