import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { MiQVMTHeaderComponent } from './header/header.component';
import { MiQVMTContent } from './content/content.component';
import { MiQVMTFooterComponent } from './footer/footer.component';

const MiQVMTComponents = [
  MiQVMTHeaderComponent,
  MiQVMTContent,
  MiQVMTFooterComponent
]

@NgModule({
  declarations: [
    AppComponent,
    ...MiQVMTComponents
  ],
  imports: [
    BrowserModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



